import { useDispatch, useSelector } from "react-redux";
import { setLevel, setName } from "../../store/settingsSlice";
import styles from "./Settings.module.css";
import { useNavigate } from "react-router-dom";

export const Settings = () => {
  const dispatch = useDispatch();
  const name = useSelector(state => state.settings.name);
  const level = useSelector(state => state.settings.level);
  let navigate = useNavigate();

  const handleChange = (e) => {
    dispatch(setName(e));
  };

  const handleStart = () => {
    if (!name && !level) {
      return alert("–ü–æ–∂–∞–ª—É–π—Å—Ç–∞, –≤–≤–µ–¥–∏—Ç–µ –∏–º—è –∏ –≤—ã–±–µ—Ä–∏—Ç–µ —Å–ª–æ–∂–Ω–æ—Å—Ç—å!");
    }

    if (!name) {
      return alert("–ü–æ–∂–∞–ª—É–π—Å—Ç–∞, –≤–≤–µ–¥–∏—Ç–µ –∏–º—è");
    }

    if (!level) {
      return alert("–ü–æ–∂–∞–ª—É–π—Å—Ç–∞, –≤—ã–±–µ—Ä–∏—Ç–µ —Å–ª–æ–∂–Ω–æ—Å—Ç—å");
    }

    return navigate("/game");
  };

  const data = [
    { value: "easy", text: "–ü—Ä–æ—Å—Ç–æ–π 8x8, 10 –º–∏–Ω" },
    { value: "medium", text: "–°—Ä–µ–¥–Ω–∏–π 16x16, 40 –º–∏–Ω" },
    { value: "hard", text: "–°–ª–æ–∂–Ω—ã–π 32x16, 100 –º–∏–Ω" }
  ];

  const setDifficulty = (value) => {
    switch (value) {
      case "easy":
        dispatch(setLevel([8, 8, 5, 10]));
        break;
      case "medium":
        dispatch(setLevel([16, 16, 20, 40]));
        break;
      case "hard":
        dispatch(setLevel([32, 16, 50, 100]));
        break;
    }
  };

  return (
    <div className={styles.settings}>
      <div>
        <h1 className={styles.subTitle}>–í–≤–µ–¥–∏—Ç–µ —Å–≤–æ–µ –∏–º—è</h1>
        <input className={styles.nameInput} value={name} onChange={e => handleChange(e.currentTarget.value)} />
      </div>
      <div className={styles.level}>
        <h1 className={styles.subTitle}>–í—ã–±–µ—Ä–∏—Ç–µ —É—Ä–æ–≤–µ–Ω—å –∏–≥—Ä—ã</h1>
        {data.map(item => (
          <label key={item.value} className={styles.label}>
            <input
              className={styles.input}
              value={item.value}
              name="settings"
              type="radio"
              onChange={e => setDifficulty(e.target.value)}
            />
            <span className={styles.span}>{item.text}</span>
          </label>
        ))}
      </div>

      <div className={styles.buttons}>
        <button onClick={() => navigate('winners')}>
          –¢–∞–±–ª–∏—Ü–∞ –ª–∏–¥–µ—Ä–æ–≤
        </button>
        <button onClick={handleStart}>
          –ù–∞—á–∞—Ç—å –∏–≥—Ä—É
        </button>
      </div>

    </div>
  );
};
